<script>
  import moment from 'moment';

  import {getToken} from '@/lib/auth';
  import request from '@/lib/axios.js';
  import {SignatureInit} from '@/lib/Signature.js'
  import {map} from 'lodash'
  import SelectFilghtInfo from "./selectFlightInfo"
  export default {
    components:{
      SelectFilghtInfo
    },
    data() {
      let validateNumber = (rule, value, callback) => {
        let reg = /^[1-9]\d{0,6}[0-9]?$/g
        if (value === '' || value === undefined || reg.test((value))) {
          callback();
        } else {
          callback(new Error('请输入不超过8位数的正整数'));
        }
      };
      let flot2Number = (rule, value, callback) => {
        let reg = /(^[1-9]\d{0,7}|^0)(\.\d{1,2})?$/g
        let reg1 = /\d+(\.\d{3,})$/
        let q = reg.test((value))
        let q1 = reg1.test(value)
        if (value === '' || value === undefined || (q)) {
          callback();
        } else {
          if (q1) {
            callback(new Error('请保留两位小数'));

          } else {
            callback(new Error('请输入不超过8位数的正数'));

          }
        }
      };
      return {
		  promiseOptions: {},// promise 返回 基础数据 是否验证用户 userIsVerify 和 默认用户名 workUser

		  airlineStatus:'',
        AirlineObj:{},
        ruleForm: {
          arrivalAirport: 'TFU',  // 起降机场
        },
        workerSignatureData: {},  // 工作者签字数据
        customerSignatureData: {},   // 机组或客户代表签字数据
        rules: {
          workDate: [{required: true, message: '请选择日期', trigger: 'blue'}],
          flightNo: [{required: true, message: '请输入航班号', trigger: 'blue'}],
        }
      }
    },
    created(){
      this.airlineStatus=this.getAirline()
      let query=this.$route.query
       if(query.type=='oms'){
          this.ruleForm={
            ...query,
            workDate:new Date(Number(query.workDate)),
            arrivalAirport: 'TFU',
          }
      }
		this.promiseOptions = new Promise((resolve, reject) => {
			request({
				url: `${this.$ip}/mms-parameter/businessDictionaryValue/listByCodes`,
				method: 'post',
				params: {delete: false, valStatus: 1},
				data: ["userIsVerify",  'QZ_Signature_workUser'  ]
			}).then(d => {
				if (d.code == 200) {

					let options = {
						// workUser: d.data.workUser[0] && d.data.workUser[0].valCode,
						workUser: {
						 	SFD: d.data.QZ_Signature_workUser[0] && d.data.QZ_Signature_workUser[0].valCode,
 						},
						userIsVerify: d.data.userIsVerify[0] && d.data.userIsVerify[0].valCode === 'true' ? 'true' : 'false',
 					}
					resolve(options)
				} else {
					reject()
				}
			}).catch(() => {
				reject()
			});
		})

	},
    watch: {
      '$route': {
        handler(to, from) {
          let Rquery =this.$route.query
           if(!Rquery){
            return false
          }
          this.ruleForm= {
            arrivalAirport: 'TFU',  // 起降机场
          };
          if(Rquery.inOrOut==='1'){
              this.$set(this.ruleForm,'inOrOut',false)
          }else if(Rquery.inOrOut==='0'){
            this.$set(this.ruleForm,'inOrOut',true)
          }

        },
        immediate:true,
        deep:true
      }
    },
    mounted() {
      let _this = this
      window.onload = function () {
        Signature.bind({
          remove: function (fn) {//签章数据撤销时，将回调此方法，需要实现签章数据持久化（保存数据到后台数据库）,
            console.log('获取删除的签章ID：' + this.getSignatureid());
            Reflect.deleteProperty(_this.workerSignatureData, this.getSignatureid())
            Reflect.deleteProperty(_this.customerSignatureData, this.getSignatureid())
            console.log(_this.workerSignatureData)
            console.log(_this.customerSignatureData)
            fn(true);//保存成功后必须回调fn(true/false)传入true/false分别表示保存成功和失败

          },
          update: function (fn) {//签章数据有变动时，将回调此方法，需要实现签章数据持久化（保存数据到后台数据库）,执行后必须回调fn(true/false)，传入true/false分别表示保存成功和失败
            console.log('获取更新的签章ID：' + this.getSignatureid());
            //console.log('获取更新的签章数据：'+this.getSignatureData());
            fn(true);
          }
        });
        var invalidSignatureArray = Signature.verify();//返回无效签章
        console.log(invalidSignatureArray)
        if (invalidSignatureArray.length > 0) {
          for (var i = 0; i < invalidSignatureArray.length; i++) {
            var signature = invalidSignatureArray[i];
            //console.log(signature.modifiedItems);//获取修改的保护项
          }
        } else {
          //Signature.alert("当前文档所有签章有效");
        }
      }
    },
    methods: {
      getAirline(){
        return new Promise((resolve,reject)=>{
          request({
            url:`${this.$ip}/config-client-mms/config/findConfigs?configName=Airline`,
            method: 'get',
          }).then(d => {
            if( d.data&&d.data.length){
              d.data.map((k,l)=>{
                if(!k.parentCode){
                  this.AirlineObj[k.iata]=k.fullname
                }
              })

              if(this.ruleForm.type=='oms'){
                this.$set(this.ruleForm,'airLine', this.AirlineObj[this.ruleForm.airLineCode])
                console.log(this.ruleForm);
              }
              resolve()
            }else{
              reject()
            }
          }).catch(()=>{
            reject()
          });
        })
      },
      clearFlightInfo( ){
        this.ruleForm={
          ...this.ruleForm,
          airLine:null,
          aircraftType:null,
          aircraftReg:null ,
          flightId:null,
        }
      },
      setFlightInfo(data){
        this.ruleForm={
          ...this.ruleForm,
          airLine:data.airline||this.AirlineObj[data.airlineCode],
          aircraftType:data.aircraftType,
          aircraftReg:data.aircraftNo,
          flightId:data.flightId,
          // flightNo:data.flightNo,
        }
        this.$message({message: '已为您填充查询到的航班信息',type:'success',duration:1000})

      },
      getFlightInfo(data){
        if(data.airline){
          this.setFlightInfo(data)
          return
        }
        this.airlineStatus.then((d)=>{
          this.setFlightInfo(data)
        }).catch(()=>{
          // this.$confirm('未获取到航司列表信息，将不能为您匹配航空公司项，是否重新获取', '提示', {
          //   confirmButtonText: '确定',
          //   cancelButtonText: '取消',
          //   type: 'warning',
          // }).then((d)=>{
          //   this.getAirline().then(()=>{
          //     this.setFlightInfo()
          //   })
          // }).catch(()=>{
          //
          // })
        })

      },
      queryFlightInfo() {
         if (this.ruleForm.workDate && this.ruleForm.flightNo) {
          let params = {
            flightNo: this.ruleForm.flightNo,
            date: moment(this.ruleForm.workDate).format('YYYY-MM-DD'),
          }
          request({
            url: `${this.$OMSip}/omms-tf-comm-web/oflight/getFlightByFlightNoAndDate`,
            method: 'get',
            params
          }).then((d) => {
            if (d.data&&d.data.length>0) {
              if (d.data.length > 1) {
                this.$refs.SelectFilghtInfo.open(d.data,this.AirlineObj)
              } else {
                this.getFlightInfo(d.data[0])
              }
            }else{
              this.$message.warning('未查询到航班信息')
            }
          })
        } else {
          this.$message.warning('日期和航班号不能为空')
        }

      },

      changeCode(key, type) {
        let value = this.ruleForm[key]
        let s
          if (type !== 'number') {
          let reg = /^(([1-9]\d{0,5})|([1-9]\d{0,7}\.)|(0\.))(\d{0,2})$/g;
          s = value.replace(/[^\d|\.]/g, '')
          s = s.replace(/(\d{0,8})(\d*)(.*)/g, '$1$3')
          let obj = reg.test(s)
          console.log('s',s);
          if (!obj) {
            let arr=s.split('.')
            if(arr.length>1){
              console.log(arr[arr.length - 1].length);
              if(arr[0]!=='0'&&!arr[0]||(arr[1]!=='0'&&!arr[1])){
                console.log('q');
                s = s.replace(/\./, '')
              }else if(arr[arr.length-1]&&arr[arr.length-1].length>=3){
                console.log('q1');
                s = s.slice(0, s.length - 1)
              }else if(arr[2]!=='0'&&!arr[2]){
                console.log('q2',arr[2]);
                s = s.slice(0, s.length - 1)
              }else if(arr[0]&&arr[0].length==8){
                console.log('q3');
                // s = s.replace(/\./, '')
                s=s.split('').reverse().join('').replace(/\./, '').split('').reverse().join('')
              }else{
                console.log('q4');
                s = s.replace(/\./, '')
              }
              // s=arr[0]+'.'+arr[1].slice(0,2)
            }else{
              if(arr[0]!=='0'){
                s = s.slice(0, s.length - 1)
              }
            }
          }
          console.log(s, obj);
          // s= value.replace(reg,'$2$3')
        } else {
          //只能输入正整数
          let reg = /([^\d]*)/g
          let reg1 = /(0*)([1-9](\d{0,7}))?(\d*)/g
          s = value.replace(reg, '')
          s = s.replace(reg1, '$2') || null

        }
        this.$set(this.ruleForm, key, s);
      },
      findByUserFn(user = {}) {
        console.log(user)
        if (user.userName && user.password) {
          return new Promise((resolve, reject) => {
            request({
              url: `${this.$ip}/sys/user/findByUser`,
              method: 'get',
              params: {
                user
              }
            }).then((result) => {
              console.log(result)
              if (result.responseCode === 1000) {
                if (result.data.length > 0) {
                  resolve('1')
                } else {
                  resolve('0')
                }
              } else {
                resolve('0')
              }
            })
          })
        }

      },
      judgeSignTypeFn(type, $event, fn) {
        this.signOthMsgBoxFn(type, $event, 'signOthFn')

      },
      signOthMsgBoxFn(type, $event, fn) {
        let _this = this
		  this.promiseOptions.then((d) => {
			  if (d.pwdIsVerify === 'true') {
				  this.$msgBox.showMsgBox({
					  isShowInput: true,
					  isShowPsd: true
				  }).then(async data => {
					  //console.log(data)
					  if (data.val && data.psd) {
						  console.log(data)
						  let judegUser = await this.findByUserFn({userName: data.val, password: data.psd})
						  if (judegUser == '1') {
							  _this[fn](type, data.val, data.psd)
						  } else {
							  this.$message({type: 'error', message: '密码错误，请重试'});
						  }
					  }
				  }).catch(() => {
					  // ...
				  });

			  } else {
				  _this[fn](type,  d.workUser['SFD'])
 			  }
		  })




      },

      signOthFn(type, val, psd) {
        SignatureInit(val, undefined, undefined, undefined)
        var signatureCreator = Signature.create();
        var that = this;
        signatureCreator.handWriteDlg({
          image_height: "1",
          image_width: "3",
          // canvas_width: "400",
          // canvas_height: "200",
          onBegin: function (qq) {
            console.log('onbegin', qq);
          },
          onEnd: function (qq) {
            console.log('onEnd', qq);
          }
        }, function (param) {
          let offsetX = 1
          $('.kg-img-div-' + type).each((ind, ele) => {
            offsetX += $(ele).width()
          })
           let arr=['item1', 'item2',  'item4', 'item5', 'item6', 'item7', 'item8',
            'item11','item13', 'item14','item17','item30','item33', 'item34','item35',]
          if(that.ruleForm.inOrOut){
            //国内定位dom Id
           let arr1=['item9', 'item10', 'item12', 'item15', 'item16', 'item36']
            arr.push(...arr1)
          }else{
            //国际定位dom Id
            let arr2=[
              'item9_1', 'item10_1', 'item9_2', 'item10_2', 'item9_3', 'item10_3',
              'item12_1','item12_2', 'item15_1',]
            arr.push(...arr2)
          }
          signatureCreator.runHW(param, {
            offsetX,
            offsetY: 1,
            protectedItems: arr, //设置定位页面DOM的id，自动查找ID，自动获取保护DOM的kg-desc属性作为保护项描述，value属性为保护数据。不设置，表示不保护数据，签章永远有效。
            position: type, //'pos3',//$("#pos3").attr('pos'),//设置盖章定位dom的ID，必须设置
            okCall: function (fn) {//点击确定后的回调方法，this为签章对象 ,签章数据撤销时，将回调此方法，需要实现签章数据持久化（保存数据到后台数据库）,保存成功后必须回调fn(true/false)渲染签章到页面上
              // console.log("盖章ID：" + this.getSignatureid());
              // console.log("盖章数据：" + this.getSignatureData());
              fn(true);
              let _this=this
              signatureCreator.getBase64Image(
                this.getSignatureid(),
                this.getSignatureData(),
                type,
                function (fn, imgdata, signid, sdata) {
                  console.log(imgdata);
                  // $('#kg-img-div-postil1').find('img').attr('src', imgdata);
                  if (type === 'pos3') {
                    _this.getSignatureid().toString()+'------'+_this.getSignatureData()+'------'+
                    imgdata[1]
                    that.workerSignatureData[_this.getSignatureid()] = _this.getSignatureid().toString()
                      + '------'+_this.getSignatureData()+'------'+ imgdata[1]
                  } else if (type === 'pos4') {
                    that.customerSignatureData[_this.getSignatureid()] =  _this.getSignatureid().toString()
                      + '------'+_this.getSignatureData()+'------'+ imgdata[1]
                  }
                  console.log(that.workerSignatureData);
                });

            },
            cancelCall: function () {//点击取消后的回调方法
              console.log("取消！")
            }
          });
        });

      },

      saveFn(form) {
        if (Object.keys(this.workerSignatureData).length === 0) {
          this.$message({type: 'warning', message: '工作者必须添加签字'})
          return
        }
        if (Object.keys(this.customerSignatureData).length === 0) {
          this.$message({type: 'warning', message: '机组或客户代表必须添加签字'})
          return
        }

        let ruleForm = this.undefinedToNullFn(this.ruleForm)
        let workerSignature = []
        map(this.workerSignatureData,(k,l,o) => {

          workerSignature.push(k)
        })
        let customerSignature = []
        map( this.customerSignatureData,(k,l) => {
          customerSignature.push(k)
        })
        Object.assign(ruleForm, {
          workerSignature: workerSignature.join(','), // 工作者签字数据
          customerSignature: customerSignature.join(','), // 机组或客户代表签字
        })
        this.$refs[form].validate(valid => {
          if (valid) {
            if(!this.ruleForm.flightId){
              this.$message.warning('请先点击查询按钮获取航班信息')
              return
            }
            request({
              url: `${this.$ip}/mms-charge/chargeBillFlxgz/save`,
              method: 'post',
              data: ruleForm
            })
              .then((data) => {
                if (data.code == 200) {
                  this.$message({type: 'success', message: '新增成功'});
                  var elements = document.querySelectorAll(".kg-img-div")
                  for (var i = elements.length - 1; i >= 0; i--) {
                    elements[i].parentNode.removeChild(elements[i]);
                  }
                  setTimeout(() => {
                    this.$router.push({path: '/news'});
                  }, 2000)
                } else {
                  this.$message({type: 'error', message: '新增失败，请重试'});
                }
              })
          }
        })


      },
      undefinedToNullFn(obj) {
        let newObj = {}
        if (Object.prototype.toString.call(obj) === "[object Object]" && Object.keys(obj).length) {
          Object.keys(obj).forEach(item => {
            if (Reflect.get(obj, item) === '') {
              Reflect.set(newObj, item, null)
            } else {
              Reflect.set(newObj, item, Reflect.get(obj, item))
            }
          })
          return newObj
        } else {
          throw new Error("ruleForm is not Object")
        }
      },

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='less'>
  .index {
    padding: 30px;
    .queryButton{
      padding:12px 18px;
    }
    .top_header {
      line-height: 68px;
      height: 68px;
      position: fixed;
      font-size: 24px;
      left: 0;
      top: 0;
      right: 0;
      background: #2D4157;
      color: #fff;
      padding: 0 10px;
      z-index: 99;
    }
    .inner {
      .header {
        font-size: 24px;
        text-align: center;
      }
      .content {
        .content_inner {
          .title {
            text-align: right;
            font-size: 20px;
            line-height: 30px;
          }
          .content_m {
            margin-top: 15px;
            .table {
              width: 100%;

              // border:1px solid #d9d9d9;
              tr {
                td {
                  padding: 10px 5px;

                  .com_flag {
                    line-height: 40px;
                    padding: 0 5px;
                  }
                  .com_flag1{
                    line-height: 20px;
                  }
                  line-height: 26px;
                  text-align: center;
                  font-size: 18px;
                  &:nth-of-type(1),  &:nth-of-type(3) {
                    width: 135px;
                    text-align: center;
                    line-height: 26px;
                  }

                  .sign_box {
                    height: 100%;
                  }
                }
              }
              td {
                line-height: 56px;
                padding: 6px;
              }
            }
          }
        }
      }
    }
  }

  .bitian {
    position: relative;
  }

  .bitian1:before {
    content: '*';
    color: #F56C6C;
    position: absolute;
    top: 3px;
    left: calc(50% - 30px);
  }

  .bitian2:before {
    content: '*';
    color: #F56C6C;
    position: absolute;
    top: 3px;
    left: calc(50% - 40px);
  }

  td {
    /deep/ .el-form-item {
      margin-bottom: 0;
    }
  }
  .keepAll{
    word-break: keep-all;
  }
</style>



